@use 'variables' as v;

h1,
h2,
h3 {
   margin: 0;
}
h1 {
   font-size: 4.4rem;
   font-family: v.$font-secondary;
}

h2 {
   font-size: 4rem;
   font-family: v.$font-secondary;
}

h3 {
   font-size: 2.8rem;
}

ul {
   padding: 0;
   list-style: none;
}

p {
   color: v.$black;
   font-size: 1.6rem;
   line-height: 1.8;
}

a {
   text-decoration: none;
}
